body {
  font-family: 'Commissioner', sans-serif;
  font-size: 14px;
  line-height: 1;
  font-weight: 400;
  color: #2c2d2b;
}
a {
  color: inherit;
  text-decoration: none;
}
img {
  max-width: 100%;
}
p,
li {
  line-height: 1.2;
}
p:last-child,
li:last-child {
  margin-bottom: 0;
}
.block-title {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0 0 25px;
}
.block-title h2 {
  font-weight: 600;
  font-size: 30px;
  margin: 0;
}
.block-title .more {
  background-color: #4bc4d7;
  color: #fff;
  border-radius: 1000px;
  margin-left: 20px;
  font-size: 14px;
  font-weight: 400;
  padding: 10px 15px;
}
.page-title {
  font-size: 40px;
  font-weight: 600;
  margin: 8px 0 30px;
}
.header {
  border-bottom: 1px solid #d5d5d5;
  position: relative;
}
.header__top {
  display: flex;
  align-items: center;
  padding: 14px 0 0;
}
.header .nav {
  display: flex;
  align-items: center;
  padding-right: 25px;
  border-right: 2px solid #808180;
}
.header .nav__item {
  border-radius: 100px;
  font-weight: 300;
  font-size: 14px;
  transition: 0.3s;
}
.header .nav__item:hover {
  color: #f68a53;
}
.header .nav__item:not(:last-of-type) {
  margin-right: 21px;
}
.header .nav__item.active {
  color: #f68a53;
  position: relative;
}
.header .nav__item.active:before {
  border-radius: 1000px;
  content: '';
  position: absolute;
  left: -10px;
  top: -6px;
  width: calc(100% + 20px);
  height: calc(100% + 12px);
  border: 1px solid #f68a53;
  z-index: -1;
}
.header .contacts {
  display: flex;
  align-items: center;
  margin-left: auto;
  padding-left: 25px;
}
.header .contacts .phone {
  font-size: 14px;
  font-weight: 600;
  margin-right: 20px;
}
.header .contacts .time {
  font-size: 14px;
  font-weight: 300;
  margin-right: 25px;
}
.header .contacts .callback {
  border-radius: 1000px;
  background-color: #f68a53;
  color: #fff;
  font-size: 14px;
  padding: 10px 16px;
  display: inline-block;
}
.header .header__bottom {
  display: flex;
  align-items: center;
  padding: 17px 0 16px;
}
.header .logo svg {
  width: 203px;
  height: 47px;
}
.header .btns {
  display: flex;
  align-items: center;
  margin-left: 23px;
}
.header .btns__item {
  padding: 12px 17px;
  border-radius: 6px;
  font-weight: 500;
  font-size: 15px;
  cursor: pointer;
  position: relative;
}
.header .btns__item:after {
  position: absolute;
  left: calc(50% - 4px);
  top: calc(100% + 11px);
  content: '';
  border: 8px solid transparent;
  border-top: 8px solid rgba(0, 21, 24, 0.651);
  opacity: 0;
  z-index: 6;
  transition: 0.1s;
}
.header .btns__item.active:after {
  opacity: 1;
  transition: 0.1s;
  transition-delay: 0.5s;
}
.header .btns__item.catalog-btn {
  background-color: #f68a53;
  color: #fff;
  margin-right: 5px;
  position: relative;
  padding-left: 47px;
}
.header .btns__item.catalog-btn svg {
  position: absolute;
  left: 14px;
  top: 11px;
  width: 23px;
  height: 16px;
  fill: #fff;
}
.header .btns__item.brands-btn {
  background-color: #4bc4d7;
  color: #fff;
}
.header .search-form {
  position: relative;
  border: 1px solid #d5d5d5;
  overflow: hidden;
  border-radius: 6px;
  margin-left: 6px;
  width: 100%;
  max-width: 430px;
}
.header .search-form__input {
  border: none;
  outline: none;
  width: 100%;
  font-size: 15px;
  font-weight: 400;
  color: #2c2d2b;
  font-family: 'Commissioner', sans-serif;
  padding: 10px 16px;
}
.header .search-form__input::placeholder {
  color: rgba(44, 45, 43, 0.4);
