  font-size: 15px;
}
.header .search-form__submit {
  background-color: #f68a53;
  color: #fff;
  width: 34px;
  height: 34px;
  position: absolute;
  right: 2px;
  top: 2px;
  border: none;
  background: none;
  outline: none;
  border-radius: 4px;
  padding: 0;
}
.header .search-form__submit svg {
  width: 19px;
  height: 18px;
  fill: #fff;
}
.header .icons {
  display: flex;
  justify-content: flex-end;
  margin-left: auto;
}
.header .icons__item {
  text-align: center;
  position: relative;
  padding: 0 10px;
  margin: 0 9px;
}
.header .icons__item .label {
  position: absolute;
  right: -13px;
  top: -7px;
  background-color: #f68a53;
  border: 1px solid #fff;
  border-radius: 100px;
  color: #fff;
  font-size: 10px;
  font-weight: 700;
  padding: 2px 5px;
}
.header .icons__item .icon {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  margin: auto;
  margin-bottom: 5px;
}
.header .icons__item .text {
  font-size: 14px;
  font-weight: 500;
}
.header .icons__item.compare svg {
  width: 14px;
  height: 18px;
  fill: #2c2d2b;
}
.header .icons__item.cabinet svg {
  width: 18px;
  height: 20px;
  fill: #2c2d2b;
}
.header .icons__item.basket svg {
  width: 19px;
  height: 20px;
  fill: #2c2d2b;
}
.header .dropdown {
  position: absolute;
  top: calc(100% - 10px);
  width: 100%;
  left: 0;
  background-color: #fff;
  z-index: 5;
  padding: 39px 0 32px;
  display: none;
}
.header .dropdown__list {
  list-style: none;
  column-count: 4;
  column-gap: 32px;
  padding: 0;
  margin: 0;
  font-size: 14px;
}
.header .dropdown__list li {
  position: relative;
  padding-left: 22px;
  margin-bottom: 13px;
}
.header .dropdown__list li:before {
  content: '';
  position: absolute;
  left: 4px;
  top: 5px;
  width: 5px;
  height: 5px;
  border-radius: 100px;
  background-color: #4bc4d7;
}
